import { useEffect, useState } from "react";
import {
  Box,
  Button,
  Checkbox,
  Container,
  Stack,
  Typography,
} from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";

const initialRows = [
  { id: 0, player: "", score: 0, bid: 0, result: false },
  { id: 1, player: "", score: 0, bid: 0, result: false },
  { id: 2, player: "", score: 0, bid: 0, result: false },
  { id: 3, player: "", score: 0, bid: 0, result: false },
  { id: 4, player: "", score: 0, bid: 0, result: false },
  { id: 5, player: "", score: 0, bid: 0, result: false },
  { id: 6, player: "", score: 0, bid: 0, result: false },
  { id: 7, player: "", score: 0, bid: 0, result: false },
];

const columns = [
  {
    field: "player",
    headerName: "Player",
    editable: true,
    flex: 5,
  },
  {
    field: "score",
    headerName: "Score",
    headerAlign: "center",
    align: "center",
    type: "number",
    editable: true,
    flex: 2,
  },
  {
    field: "bid",
    headerName: "Bid",
    headerAlign: "center",
    align: "center",
    type: "number",
    editable: true,
    flex: 2,
  },
  {
    field: "result",
    headerName: "Result",
    headerAlign: "center",
    align: "center",
    flex: 2,
    editable: false,

    renderCell: (params) => {
      return (
        <Checkbox
          checked={params.row.result}
          onMouseDown={(event) => {
            // Prevent the default click event
            event.stopPropagation();
          }}
        />
      );
    },
  },
];

export default function App() {
  const [rowData, setRowData] = useState(
    JSON.parse(localStorage.getItem('rowData')) || initialRows
  );
  const [roundNumber, setRoundNumber] = useState(
    JSON.parse(localStorage.getItem('roundNumber')) || 1
  );

  useEffect(() => {
    localStorage.setItem('rowData', JSON.stringify(rowData));
    localStorage.setItem('roundNumber', JSON.stringify(roundNumber));
  }, [rowData, roundNumber]);

  const submit = () => {
    setRoundNumber((prevRoundNumber) => prevRoundNumber + 1);
    setRowData((prevRows) => {
      return prevRows.map((row, index) => {
        var rowScore = row.score + 10 + row.bid;
        return {
          ...row,
          score: row.result ? rowScore : row.score,
          bid: 0,
          result: false,
        };
      });
    });
  };

  const handleCheckboxChange = (event, row) => {
    // Find the index of the row that was edited
    const rowIndex = rowData.findIndex((r) => r.id === row.id);

    // Replace the old row with the updated row
    const updatedRows = [...rowData];
    updatedRows[rowIndex] = {
      ...row,
      result: event.target.checked,
    };

    // Update the state with the new rows
    setRowData(updatedRows);
  };

  const handleProcessRowUpdate = (updatedRow, originalRow) => {
    // Find the index of the row that was edited
    const rowIndex = rowData.findIndex((row) => row.id === updatedRow.id);

    // // Replace the old row with the updated row
    const updatedRows = [...rowData];
    updatedRows[rowIndex] = updatedRow;

    // Update the state with the new rows
    setRowData(updatedRows);

    // Return the updated row to update the internal state of the DataGrid
    return updatedRow;
  };

  const clearGame = () => {
    setRoundNumber(1);
    setRowData((prevRows) => {
      return prevRows.map((row, index) => {
        return {
          ...row,
          score: 0,
          bid: 0,
          result: false,
        };
      });
    });
  };

  return (
    <Box sx={{ height: "100%", width: "100%" }}>
      <DataGrid
        rows={rowData}
        columns={columns}
        processRowUpdate={handleProcessRowUpdate}
        editMode="cell"
        disableColumnMenu
        disableColumnResize
        disableColumnSorting
        disableRowSelectionOnClick
        hideFooter
        rowHeight={50}
        onCellClick={(params, event) => {
          // Check if the clicked cell is a checkbox cell
          if (params.field === "result") {
            // Prevent the default click event
            event.defaultMuiPrevented = true;

            // Toggle the checkbox
            handleCheckboxChange(
              { target: { checked: !params.value } },
              params.row
            );
          }
        }}
      />
      <Container sx={{ marginTop: 2 }}>
        <Stack direction="column" spacing={2}>
          <Stack direction="row" spacing={2} justifyContent="space-between">
            <Typography variant="h6">
              Cards: {roundNumber <= 13 ? roundNumber : 13 - (roundNumber - 13)}
            </Typography>
            <Typography variant="h6">Round: {roundNumber}</Typography>
            <Typography variant="h6">
              Total Bids: {rowData.reduce((sum, row) => sum + row.bid, 0)}
            </Typography>
          </Stack>
          <Button variant="contained" color="primary" onClick={submit}>
            Submit Round
          </Button>
          <Button variant="contained" color="primary" onClick={clearGame}>
            Clear Game
          </Button>
        </Stack>
      </Container>
    </Box>
  );
}
